footer {
	background: #111;
	.inner {
		width: 1180px;
		margin: 0 auto;
		position: relative;
		padding: 80px 0 50px;
		display: flex;
		justify-content: space-between;
		.text {
			width: 30%;
			.footLogo {
				display: inline-block;
				width: 60px;
				height: 60px;
				background: #4b84db;
				text-align: center;
				font-size: 20px;
				font-weight: bold;
				line-height: 60px;
				color: #e1f9fe;
				margin-bottom: 30px;
			}
			p {
				font-size: 12px;
				color: #555;
				line-height: 1.6;
				margin-bottom: 30px;
			}
			.sns {
				display: flex;
				a {
					display: block;
					width: 50px;
					height: 50px;
					line-height: 50px;
					text-align: center;
					border-radius: 50%;
					background: #333;
					font-size: 16px;
					color: #fff;
					margin-right: 5px;
				}
			}
		}
		.link {
			width: 20%;
			p {
				font-size: 12px;
				color: #555;
				line-height: 1.6;
				margin-bottom: 20px;
				strong {
					display: block;
					font-size: 16px;
					color: #fff;
					margin-bottom: 20px;
				}
			}
			ul {
				li {
					margin-bottom: 10px;
					a {
						font-size: 12px;
						color: #fff;
					}
				}
			}
		}
	}
	.copy {
		width: 100%;
		height: 85px;
		line-height: 85px;
		background: #333;
		text-align: center;
		color: #777;
	}
}

@media screen and (max-width: $tablet) {
	footer {
		.inner {
			width: 95%;
		}
	}
}

@media screen and (max-width: $mobile) {
	footer {
		.inner {
			flex-wrap: wrap;
			.text {
				width: 100%;
				.sns {
					position: absolute;
					top: 90px;
					right: 5%;
					a {
						margin-right: 0px;
						margin-left: 10px;
					}
				}
			}
			.link {
				width: calc((100% / 3) - 1%);
			}
		}
		.btnTop {
			bottom: 2%;
		}
	}
}
